.post {
    max-width: 500px;
    background-color: white;
    border: 1px solid lightgray;
    margin: 45px 0;
}
.post__image {
    width: 100%;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
}

.post__text{
    font-weight: normal;
    padding: 20px;
}

.post__header{
    display: flex;
    align-items: center;
    padding: 20px;
}

.post__avatar {
    margin-right: 10px;
}
